import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_ChangeSessionAuth,
  FT_ChangeSessionAuth,
  loadEducationCategories,
  schoolLevelsStoreInterface,
} from "../../actions";
import { education_category } from "../../interfaces";
import { StoreState } from "../../reducers";

interface ChooseInstituteProps {
  auth: Auth;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  FC_ChangeSessionAuth: FT_ChangeSessionAuth;
}

interface ChooseInstituteState {
  loading: boolean;
  institution_id: string;
}

class ChooseInstitute extends Component<
  ChooseInstituteProps,
  ChooseInstituteState
> {
  constructor(props: ChooseInstituteProps) {
    super(props);

    this.state = {
      loading: false,
      institution_id: this.props.auth.application_session
        ? this.props.auth.application_session._id
        : "",
    };
  }

  componentDidMount() {
    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.props.loadEducationCategories(
        (status: boolean, data?: education_category[]) => {
          this.setState({ loading: status });
        }
      );
    }
  }

  selectInstitution = (institution_id: string) => {
    if (this.props.schoolLevels.education_categories) {
      let data = this.props.schoolLevels.education_categories.find(
        (item) => item.education_institute_id === institution_id
      );
      if (data) {
        this.props.FC_ChangeSessionAuth(data.session_plan);
        this.setState({ institution_id: data.education_institute_id });
      }
    }
  };

  render() {
    return (
      <>
        <select
          title="Select Institution"
          className="p-1 border rounded w-auto text-lg bg-blue-100 border-blue-300 text-blue-900"
          onChange={(e) => this.selectInstitution(e.target.value)}
          disabled={
            this.props.schoolLevels.education_categories === null ||
            this.props.schoolLevels.education_categories.length === 0
          }
          value={this.state.institution_id}
        >
          <option value="">Choose</option>
          {this.props.schoolLevels.education_categories &&
            this.props.schoolLevels.education_categories.map((item, i) => (
              <option
                key={i + item.education_institute_id}
                value={item.education_institute_id}
              >
                {item.education_category_title}
              </option>
            ))}
        </select>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    schoolLevels: schoolLevels,
  };
};
export default connect(mapStateToProps, {
  loadEducationCategories,
  FC_ChangeSessionAuth,
})(ChooseInstitute);
